import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {ITransferServiceProvider} from "../../providers/i-transfer-service/i-transfer-service";
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";


@IonicPage()
@Component({
  selector: 'page-i-transfer02',
  templateUrl: 'i-transfer02.html',
})
export class ITransfer02Page {
  iTransferError02;

  accountNo;
  transferDetails01;
  purposeList;
  destAccList;
  srcAccDetails;
  selectedAccount;
  destAcc = "";
  purposeID = "";
  purposeTitle = "";
  destAccTitle = "";
  showVar = false;
  formVal = {amount: "", note: ""};
  amount = "";
  note = "";

  formInputValue = {purposeID: "", purposeTitle: "", amount: "", note: ""};


  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public cryptoUtil: CryptoParamUtilProvider,
              public maskTokenUtil: MaskTokenProvider,
              public storage: LocalStorageProvider,
              public alertCtrl: AlertController,
              public iTransferService: ITransferServiceProvider,
              public tokenService: TokenServiceProvider) {

    this.accountNo = this.navParams.get("accountNo");
    this.transferDetails01 = JSON.parse(this.navParams.get("transferDetails01"));
    this.purposeList = this.transferDetails01.ftPurposeList;
    this.destAccList = this.transferDetails01.destAccList;
    this.srcAccDetails = this.transferDetails01.accInfoBean;
    let sourcePage = JSON.parse(this.navParams.get("sourcePage"));

    if (sourcePage == "transfer") {
      this.destAcc = "";
      this.destAccTitle = "";
      this.purposeID = "";
      this.purposeTitle = "";
      this.formVal = {amount: "", note: ""};
      this.showVar = false;
    }

    else {
      this.selectedAccount = JSON.parse(this.navParams.get("selectedAccount"));
      this.formInputValue = JSON.parse(this.navParams.get("formInputValue"));
      this.destAcc = this.selectedAccount.subscriptionValue;
      this.destAccTitle = this.selectedAccount.subscriptionDesc;
      this.purposeID = this.formInputValue.purposeID;
      this.purposeTitle = this.formInputValue.purposeTitle;
      this.formVal = {amount: this.formInputValue.amount, note: this.formInputValue.note};
      this.showVar = true;
    }
  }


  iTransfer02Action = function () {
    this.iTransferError02 = "";

    let validateAmountExp = new RegExp("[0-9]{1,6}$");
    let flag = true;
    this.amount = this.formVal.amount;
    this.note = this.formVal.note;

    if (this.destAcc == "" || typeof(this.destAcc) == "undefined") {
      this.iTransferError02 = "Please Select Destination Account.";
      flag = false;
      return;
    }

    if (this.purposeID == "" || typeof(this.purposeID) == "undefined") {
      this.iTransferError02 = "Please Select Transfer Purpose.";
      flag = false;
      return;
    }

    if (this.amount == "" || typeof(this.amount) == "undefined") {
      this.iTransferError02 = "Transfer amount can not be blank.";
      flag = false;
      return;
    }
    if (!validateAmountExp.test(this.amount)) {
      this.iTransferError02 = "Invalid Transfer amount.";
      flag = false;
      this.formVal = {amount: ""};
      return;
    }


    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encSrcAccNo = this.cryptoUtil.getEncryptVal(this.accountNo);
      let encDestAccountNo = this.cryptoUtil.getEncryptVal(this.destAcc);
      let encAmount = this.cryptoUtil.getEncryptVal(this.amount);
      let encPurpose = this.cryptoUtil.getEncryptVal(this.purposeID);
      let encNote = this.cryptoUtil.getEncryptVal(this.note);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.iTransferService.initFundTransfer(unMaskToken, appData.userId, appData.appId, encSrcAccNo, encDestAccountNo, encAmount, encNote, encPurpose)
        .then(data => {
          this.onInitFundTransferComplete(data);
        });
    }
  };

  onInitFundTransferComplete = function (data) {

    if (data.statusCode == 1) {
      this.navCtrl.push('ITransfer03Page', {
        'srcAccTitle': this.srcAccDetails.accountTitle,
        'amount': this.amount,
        'purpose': this.purposeTitle,
        'initTransferResponse': JSON.stringify(data.object)
      });
    }

    else if (data.statusCode == 0) {
      this.iTransferError02 = data.note;
    }

    else {
      this.iTransferError02 = "System Error Contact with Administrator.";
    }
  };

  selectDestinationAccount = function () {
    if (this.destAccList == null) {
      this.iTransferError02 = "You have no destination account subscription. Please subscribe first.";
      return;
    }
    this.formInputValue = {
      purposeID: this.purposeID,
      purposeTitle: this.purposeTitle,
      amount: this.formVal.amount,
      note: this.formVal.note
    };
    this.navCtrl.push('ITransferDestAccountPage', {
      'accountNo': this.accountNo,
      'transferDetails01': JSON.stringify(this.transferDetails01),
      'formInputValue': JSON.stringify(this.formInputValue)
    })
  };

  showPurposePopup01 = function () {

    let data = this.purposeList;
    let radioAlert = this.alertCtrl.create();
    radioAlert.setTitle('Select Purpose');
    radioAlert.setCssClass("alertLogCss");

    Object.keys(data).forEach(key => {
      let value = data[key];
      radioAlert.addInput({
        type: 'radio',
        label: value.Title,
        value: value,
        checked: false
      })
    });

    radioAlert.addButton('Cancel');
    radioAlert.addButton({
      text: 'OK',
      handler: data => {
        if (typeof(data) == "undefined") {
        }

        else {

          this.purposeTitle = data.Title;
          this.purposeID = data.ID;
          this.iTransferError02 = "";
        }
      }
    });
    radioAlert.present();
    setTimeout(() => {
      radioAlert.dismiss();
    }, 30000);
  };


  ionViewDidLoad() {
  }

}



